import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import {
  Box,
  Typography,
  Button,
  TextField,
  Paper,
  Grid,
  makeStyles,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormGroup,
  FormControlLabel,
  Checkbox,
} from '@material-ui/core';
import Header from '../components/header';
import Footer from '../components/footer';

const TRANSITION = 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)';

const useStyles = makeStyles((theme) => ({
  '@keyframes fadeIn': {
    from: { opacity: 0, transform: 'translateY(12px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
  },
  '@keyframes fadeInUp': {
    from: { opacity: 0, transform: 'translateY(24px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
  },
  root: {
    minHeight: '100vh',
    display: 'flex',
    flexDirection: 'column',
    background: 'linear-gradient(180deg, #fafafa 0%, #f5f5f5 30%, #f0f0f0 70%, #f5f5f5 100%)',
  },
  main: {
    flex: 1,
    paddingTop: 100,
    paddingBottom: theme.spacing(10),
    animation: '$fadeIn 0.5s ease-out',
    [theme.breakpoints.down('sm')]: { paddingTop: 88, paddingBottom: theme.spacing(6) },
    [theme.breakpoints.between('sm', 'md')]: { paddingTop: 96 },
  },
  container: {
    maxWidth: 560,
    margin: '0 auto',
    padding: theme.spacing(3, 2),
    width: '100%',
    boxSizing: 'border-box',
    [theme.breakpoints.down('sm')]: { padding: theme.spacing(2, 1.5) },
    [theme.breakpoints.between('sm', 'md')]: { padding: theme.spacing(2.5, 2), maxWidth: 520 },
  },
  title: {
    fontWeight: 600,
    fontSize: '1.75rem',
    color: '#111',
    marginBottom: theme.spacing(1),
    textAlign: 'center',
    letterSpacing: '-0.02em',
    [theme.breakpoints.down('sm')]: { fontSize: '1.5rem' },
  },
  subtitle: {
    color: '#666',
    fontSize: '0.9375rem',
    textAlign: 'center',
    marginBottom: theme.spacing(5),
    lineHeight: 1.6,
  },
  formPaper: {
    padding: theme.spacing(4, 3),
    borderRadius: 16,
    [theme.breakpoints.down('sm')]: { padding: theme.spacing(2.5, 2) },
    [theme.breakpoints.between('sm', 'md')]: { padding: theme.spacing(3, 2.5) },
    boxShadow: '0 2px 12px rgba(0,0,0,0.04), 0 1px 2px rgba(0,0,0,0.04)',
    border: '1px solid rgba(234, 88, 12, 0.12)',
    borderTop: '3px solid #ea580c',
    background: '#fff',
    transition: TRANSITION,
    animation: '$fadeInUp 0.4s ease-out',
    '&:hover': { boxShadow: '0 8px 24px rgba(234, 88, 12, 0.06)' },
  },
  planChip: {
    display: 'inline-block',
    padding: theme.spacing(0.5, 1.5),
    borderRadius: 6,
    color: '#fff',
    fontWeight: 500,
    fontSize: '0.8125rem',
    marginBottom: theme.spacing(3),
  },
  field: {
    marginBottom: theme.spacing(2.5),
    '& .MuiOutlinedInput-root': {
      borderRadius: 8,
      transition: TRANSITION,
      '&:hover .MuiOutlinedInput-notchedOutline': { borderColor: '#ccc' },
    },
  },
  submitButton: {
    background: 'linear-gradient(135deg, #ea580c 0%, #f97316 100%)',
    color: '#fff',
    fontWeight: 600,
    padding: theme.spacing(1.5, 4),
    marginTop: theme.spacing(1),
    textTransform: 'none',
    borderRadius: 10,
    boxShadow: '0 4px 14px rgba(234, 88, 12, 0.35)',
    transition: TRANSITION,
    '&:hover': {
      background: 'linear-gradient(135deg, #c2410c 0%, #ea580c 100%)',
      boxShadow: '0 6px 20px rgba(234, 88, 12, 0.4)',
      transform: 'translateY(-1px)',
    },
  },
  successBox: {
    animation: '$fadeInUp 0.5s ease-out',
  },
  recommendedSection: {
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2.5),
    borderRadius: 12,
    background: 'linear-gradient(135deg, rgba(234, 88, 12, 0.04) 0%, rgba(249, 115, 22, 0.02) 100%)',
    border: '1px solid rgba(234, 88, 12, 0.15)',
  },
  recommendedTitle: {
    fontWeight: 600,
    fontSize: '0.9375rem',
    color: '#0f172a',
    marginBottom: theme.spacing(1.5),
    paddingBottom: theme.spacing(1),
    borderBottom: '2px solid #ea580c',
  },
  recommendedSubtitle: {
    fontSize: '0.8125rem',
    color: '#64748b',
    marginBottom: theme.spacing(1.5),
    lineHeight: 1.5,
  },
  recommendedList: {
    display: 'flex',
    flexDirection: 'column',
    gap: theme.spacing(0.5),
  },
  recommendedItem: {
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(1),
    fontSize: '0.9375rem',
    color: '#334155',
    '& .MuiFormControlLabel-label': { fontSize: '0.9375rem' },
    '& .MuiCheckbox-root.Mui-checked': { color: '#ea580c' },
  },
  recommendedItemDesc: {
    color: '#64748b',
    fontWeight: 400,
  },
}));

/* Recommended additions by plan */
const RECOMMENDED_ADDITIONS = {
  starter: [
    { id: 'biometric', label: 'Biometric time logs integration', desc: 'Fingerprint scanners & attendance sync' },
    { id: 'leave', label: 'Leave credits & approvals', desc: 'Streamline leave management' },
    { id: 'compliance', label: 'SSS, Pag-IBIG, PhilHealth reports', desc: 'Statutory compliance made easy' },
  ],
  professional: [
    { id: 'hr-approvals', label: 'HR approval workflows', desc: 'OT, leave, official business approvals' },
    { id: 'portal', label: 'Self-service employee portal', desc: 'Payslips, leave requests, profile updates' },
    { id: 'branches', label: 'Multi-branch support', desc: 'Manage payroll across locations' },
    { id: 'loans', label: 'Loan & cash advance management', desc: 'Automated deductions & amortization' },
  ],
  enterprise: [
    { id: '24-7', label: '24/7 priority support', desc: 'Dedicated product experts' },
    { id: 'dashboards', label: 'Management & analytics dashboards', desc: 'Data-driven insights' },
    { id: 'custom', label: 'Custom formulas & bulk updates', desc: 'Flexibility for complex setups' },
    { id: '201file', label: '201 file management', desc: 'Employee records & documentation' },
  ],
};

const PLANS = [
  { id: 'starter', name: 'Starter', color: '#22c55e' },
  { id: 'professional', name: 'Professional', color: '#3b82f6' },
  { id: 'enterprise', name: 'Enterprise', color: '#8b5cf6' },
];

function BookADemo() {
  const classes = useStyles();
  const location = useLocation();
  const planFromState = location.state?.plan;

  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    company: '',
    phone: '',
    plan: planFromState?.id || 'professional',
    preferredDate: '',
    preferredTime: '',
    message: '',
  });
  const [selectedAdditions, setSelectedAdditions] = useState([]);
  const [submitted, setSubmitted] = useState(false);

  const currentAdditions = RECOMMENDED_ADDITIONS[formData.plan] || RECOMMENDED_ADDITIONS.professional;

  const toggleAddition = (id) => {
    setSelectedAdditions((prev) =>
      prev.includes(id) ? prev.filter((a) => a !== id) : [...prev, id]
    );
  };

  useEffect(() => {
    setSelectedAdditions([]);
  }, [formData.plan]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setSubmitted(true);
  };

  if (submitted) {
    return (
      <div className={classes.root}>
        <Header />
        <main className={classes.main}>
          <Box className={`${classes.container} ${classes.successBox}`} style={{ textAlign: 'center' }}>
            <Typography className={classes.title}>Thank you</Typography>
            <Typography className={classes.subtitle}>
              Your demo request has been submitted. We'll contact you within 24 hours to schedule your personalized UlapPayroll demo.
            </Typography>
          </Box>
        </main>
        <Footer />
      </div>
    );
  }

  return (
    <div className={classes.root}>
      <Header />
      <main className={classes.main}>
        <Box className={classes.container}>
          <Typography className={classes.title}>Book a Demo</Typography>
          <Typography className={classes.subtitle}>
            Schedule a personalized demo and see how UlapPayroll can streamline your payroll.
          </Typography>

          <Paper className={classes.formPaper}>
            <form onSubmit={handleSubmit}>
              {planFromState && (
                <Box style={{ marginBottom: 24 }}>
                  <Typography variant="caption" color="textSecondary" style={{ display: 'block', marginBottom: 6 }}>
                    Recommended from Product Finder
                  </Typography>
                  <span
                    className={classes.planChip}
                    style={{ background: planFromState.color }}
                  >
                    {planFromState.name}  {planFromState.price}/mo
                  </span>
                </Box>
              )}

              <Grid container spacing={2}>
                <Grid item xs={12} sm={6}>
                  <TextField
                    className={classes.field}
                    fullWidth
                    required
                    label="Full Name"
                    name="fullName"
                    value={formData.fullName}
                    onChange={handleChange}
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    className={classes.field}
                    fullWidth
                    required
                    label="Work Email"
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={handleChange}
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    className={classes.field}
                    fullWidth
                    required
                    label="Company Name"
                    name="company"
                    value={formData.company}
                    onChange={handleChange}
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    className={classes.field}
                    fullWidth
                    label="Phone Number"
                    name="phone"
                    value={formData.phone}
                    onChange={handleChange}
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12}>
                  <FormControl variant="outlined" fullWidth className={classes.field}>
                    <InputLabel>Plan of Interest</InputLabel>
                    <Select
                      name="plan"
                      value={formData.plan}
                      onChange={handleChange}
                      label="Plan of Interest"
                    >
                      {PLANS.map((p) => (
                        <MenuItem key={p.id} value={p.id}>
                          {p.name}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>

                <Grid item xs={12}>
                  <Box className={classes.recommendedSection}>
                    <Typography className={classes.recommendedTitle}>Recommended Additions</Typography>
                    <Typography className={classes.recommendedSubtitle}>
                      Select any add-ons you&apos;d like to explore during your demo. We&apos;ll tailor the session to your interests.
                    </Typography>
                    <FormGroup className={classes.recommendedList}>
                      {currentAdditions.map((add) => (
                        <FormControlLabel
                          key={add.id}
                          control={
                            <Checkbox
                              checked={selectedAdditions.includes(add.id)}
                              onChange={() => toggleAddition(add.id)}
                              color="primary"
                            />
                          }
                          label={
                            <span>
                              <strong>{add.label}</strong>
                              {add.desc && <span className={classes.recommendedItemDesc}>  {add.desc}</span>}
                            </span>
                          }
                          className={classes.recommendedItem}
                        />
                      ))}
                    </FormGroup>
                  </Box>
                </Grid>

                <Grid item xs={12} sm={6}>
                  <TextField
                    className={classes.field}
                    fullWidth
                    label="Preferred Date"
                    name="preferredDate"
                    type="date"
                    value={formData.preferredDate}
                    onChange={handleChange}
                    variant="outlined"
                    InputLabelProps={{ shrink: true }}
                  />
                </Grid>
                <Grid item xs={12} sm={6}>
                  <TextField
                    className={classes.field}
                    fullWidth
                    label="Preferred Time"
                    name="preferredTime"
                    type="time"
                    value={formData.preferredTime}
                    onChange={handleChange}
                    variant="outlined"
                    InputLabelProps={{ shrink: true }}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    className={classes.field}
                    fullWidth
                    multiline
                    rows={4}
                    label="Message (optional)"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    variant="outlined"
                    placeholder="Tell us about your payroll needs or questions..."
                  />
                </Grid>
              </Grid>

              <Button
                type="submit"
                variant="contained"
                className={classes.submitButton}
                size="large"
                fullWidth
              >
                Schedule Demo
              </Button>
            </form>
          </Paper>
        </Box>
      </main>
      <Footer />
    </div>
  );
}

export default BookADemo;
