/**
 * Smoothly scrolls to a target position or element.
 * Uses requestAnimationFrame for consistent animation across browsers.
 */
export function smoothScrollTo(target, duration = 800) {
  const startY = window.pageYOffset;
  const targetY = typeof target === 'number' ? target : (target?.getBoundingClientRect?.()?.top ?? 0) + startY;
  const distance = targetY - startY;
  const startTime = performance.now();

  function easeInOutCubic(t) {
    return t < 0.5 ? 4 * t * t * t : 1 - Math.pow(-2 * t + 2, 3) / 2;
  }

  function step(currentTime) {
    const elapsed = currentTime - startTime;
    const progress = Math.min(elapsed / duration, 1);
    const eased = easeInOutCubic(progress);
    window.scrollTo(0, startY + distance * eased);
    if (progress < 1) requestAnimationFrame(step);
  }

  requestAnimationFrame(step);
}
