import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import {
  AppBar,
  Toolbar,
  Button,
  Typography,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';
import logoImage from '../images/payrolllogo.png';

const TRANSITION = 'all 0.25s cubic-bezier(0.4, 0, 0.2, 1)';

const useStyles = makeStyles((theme) => ({
  appBar: {
    background: '#f5f5f5',
    WebkitBackdropFilter: 'blur(12px) saturate(180%)',
    backdropFilter: 'blur(12px) saturate(180%)',
    boxShadow: '0 1px 0 rgba(0, 0, 0, 0.08), 0 4px 16px rgba(0, 0, 0, 0.06)',
    borderBottom: '1px solid rgba(0, 0, 0, 0.1)',
    transition: TRANSITION,
  },
  toolbar: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    padding: theme.spacing(1.75, 3),
    minHeight: 64,
    color: '#1f2937',
    maxWidth: 1200,
    margin: '0 auto',
    width: '100%',
    [theme.breakpoints.down('sm')]: {
      padding: theme.spacing(1.25, 1.5),
      minHeight: 56,
    },
    [theme.breakpoints.between('sm', 'md')]: { padding: theme.spacing(1.5, 2) },
  },
  logo: {
    display: 'flex',
    alignItems: 'center',
    gap: theme.spacing(1.25),
    textDecoration: 'none',
    color: 'inherit',
    transition: TRANSITION,
    '&:hover': {
      opacity: 0.9,
      transform: 'translateY(-0.5px)',
    },
  },
  logoImage: {
    height: 32,
    width: 'auto',
    objectFit: 'contain',
    [theme.breakpoints.down('sm')]: { height: 26 },
  },
    background: 'linear-gradient(135deg, #ea580c 0%, #c2410c 100%)',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
    backgroundClip: 'text',
  logoText: {
    fontWeight: 700,
    fontSize: '1.3rem',
    color: '#ea580c',
    letterSpacing: '-0.025em',
    background: 'linear-gradient(135deg, #ea580c 0%, #c2410c 100%)',
    WebkitBackgroundClip: 'text',
    WebkitTextFillColor: 'transparent',
    backgroundClip: 'text',
    [theme.breakpoints.down('sm')]: { fontSize: '1.15rem' },
  },
  navLinks: {
    display: 'flex',
    gap: theme.spacing(0.5),
    alignItems: 'center',
    [theme.breakpoints.down('md')]: { display: 'none' },
  },
  navLink: {
    color: '#1f2937',
    textTransform: 'none',
    fontWeight: 500,
    fontSize: '0.9375rem',
    letterSpacing: '-0.01em',
    padding: theme.spacing(1, 1.75),
    borderRadius: 8,
    transition: TRANSITION,
    '&:hover': {
      color: '#ea580c',
      backgroundColor: 'rgba(234, 88, 12, 0.08)',
      transform: 'translateY(-1px)',
    },
  },
  navLinkActive: {
    color: '#ea580c',
    fontWeight: 600,
    backgroundColor: 'rgba(234, 88, 12, 0.1)',
    boxShadow: 'inset 0 0 0 1px rgba(234, 88, 12, 0.2)',
    '&:hover': {
      color: '#c2410c',
      backgroundColor: 'rgba(234, 88, 12, 0.14)',
      boxShadow: 'inset 0 0 0 1px rgba(234, 88, 12, 0.25)',
    },
  },
  menuButton: {
    display: 'none',
    color: '#1f2937',
    transition: TRANSITION,
    '&:hover': {
      backgroundColor: 'rgba(234, 88, 12, 0.08)',
      color: '#ea580c',
    },
    [theme.breakpoints.down('md')]: {
      display: 'flex',
    },
  },
  drawer: {
    '& .MuiDrawer-paper': {
      width: 300,
      paddingTop: theme.spacing(2),
      borderLeft: '1px solid rgba(0, 0, 0, 0.06)',
      boxShadow: '-4px 0 24px rgba(0, 0, 0, 0.08)',
      background: '#f0f0f0',
      WebkitBackdropFilter: 'blur(12px)',
      backdropFilter: 'blur(12px)',
    },
  },
  drawerHeader: {
    display: 'flex',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 2, 1),
    borderBottom: '1px solid rgba(0, 0, 0, 0.06)',
    marginBottom: theme.spacing(1),
  },
  drawerList: {
    padding: theme.spacing(1.5, 2),
  },
  drawerItem: {
    padding: theme.spacing(1.5, 2),
    borderRadius: 10,
    marginBottom: theme.spacing(0.5),
    transition: TRANSITION,
    '&:hover': {
      backgroundColor: 'rgba(234, 88, 12, 0.08)',
      paddingLeft: theme.spacing(2.5),
    },
  },
  drawerItemActive: {
    backgroundColor: 'rgba(234, 88, 12, 0.1)',
    color: '#ea580c',
    boxShadow: 'inset 0 0 0 1px rgba(234, 88, 12, 0.2)',
    '& .MuiListItemText-primary': { fontWeight: 600 },
    '&:hover': {
      backgroundColor: 'rgba(234, 88, 12, 0.14)',
    },
  },
}));

const navItems = [
  { label: 'UlapPayroll', path: '/ulapayroll' },
  { label: 'Product Finder', path: '/productfinder' },
  { label: 'Pricing Plans', path: '/checkout' },
  { label: 'Book a Demo', path: '/bookademo' },
];


function isActivePath(currentPath, itemPath) {
if (itemPath === '/ulapayroll') {
    return currentPath === '/ulapayroll' || currentPath === '/ulapayroll/';
  }
  return currentPath === itemPath || currentPath.startsWith(itemPath + '/');
}

export default function Header() {
  const classes = useStyles();
  const { pathname } = useLocation();
  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleDrawerToggle = () => setDrawerOpen(!drawerOpen);

  return (
    <>
      <AppBar position="fixed" className={classes.appBar} color="default">
        <Toolbar className={classes.toolbar}>
          <Link to="/ulapayroll" className={classes.logo}>
            <img src={logoImage} alt="UlapPayroll" className={classes.logoImage} />
            <Typography className={classes.logoText} variant="h6">
              UlapPayroll
            </Typography>
          </Link>

          <div className={classes.navLinks}>
            {navItems.map(({ label, path }) => (
              <Button
                key={path}
                className={isActivePath(pathname, path) ? `${classes.navLink} ${classes.navLinkActive}` : classes.navLink}
                component={Link}
                to={path}
              >
                {label}
              </Button>
            ))}
          </div>

          <IconButton
            className={classes.menuButton}
            edge="end"
            color="inherit"
            aria-label="menu"
            onClick={handleDrawerToggle}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      <Drawer
        className={classes.drawer}
        anchor="right"
        open={drawerOpen}
        onClose={handleDrawerToggle}
      >
        <div className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerToggle}>
            <CloseIcon />
          </IconButton>
        </div>
        <List className={classes.drawerList}>
          {navItems.map(({ label, path }) => (
            <ListItem
              key={path}
              button
              className={isActivePath(pathname, path) ? `${classes.drawerItem} ${classes.drawerItemActive}` : classes.drawerItem}
              onClick={handleDrawerToggle}
              component={Link}
              to={path}
            >
              <ListItemText primary={label} />
            </ListItem>
          ))}
        </List>
      </Drawer>
    </>
  );
}
