function ScrollDown() {
  const handleClick = (e) => {
    e.preventDefault();
    const el = document.getElementById('overview');
    if (el) smoothScrollTo(el, 800);
  };
  const classes = useStyles();
  return (
    <a href="#overview" onClick={handleClick} className={classes.scrollIndicator} aria-label="Scroll to overview">
      <div className={classes.scrollOval}>
        <div className={classes.scrollDot} />
      </div>
      <Typography variant="caption">Scroll Down</Typography>
    </a>
  );
}

